<template>
  <div id="hashrate" class="mk-panel hashrate">
    <div class="hashrate-caption">
      <h2>Hashrate</h2>
      <small>*historical chart</small>
    </div>
    <div class="mk-panel-body">      
      <div class="algorythm-buttons">
        <buttons-group :buttons="algorythms" :onClick="onAlgorythmChanged" />
      </div>
      <div class="clearfix"></div>
      <div class="chart-container">
        <!--
        <canvas id="hashrateHistoricalChart"></canvas>
        -->
        <hashrate-chart :chart-data="datacollection" :options="hashrateOptions" />
      </div>
    </div>
  </div>
</template>

<script>
import minekamp from '@/api/minekamp';
import ButtonsGroup from '@/components/elements/ButtonsGroup';
import HashrateChart from '@/charts/HashrateChart.js'

export default {
  components : {
    ButtonsGroup,
    HashrateChart
  },
  data() {
    return {
      algorythms : [
        { id: "sha256", caption: "SHA-256" },
        { id: "scrypt", caption: "Scrypt" },
        { id: "x11", caption: "x11" }
      ],
      hashrate: [],
      datacollection : null,
      hashrateBackground : null,
      hashrateOptions : {
          responsive: true,
          
          scales: {
            xAxes: [{              
              ticks: {
                beginAtZero: true,                
              },
              gridLines: {
                zeroLineColor:"#82e6f6",
                color: "transparent",
                display: true,
                drawBorder: false,
                zeroLineWidth: 1
              }
            }],
            yAxes: [{
              ticks: {
                beginAtZero:true,
                callback: function(tick) {
                  return tick.toString() + ' TH/s';
                }
              },
              gridLines: {                
                zeroLineColor:"#82e6f6",
                color: "transparent",
                display: true,
                drawBorder: false,
                zeroLineWidth: 1
              }
            }]
          },
          elements: { 
            point: { 
              radius: 0 
            } 
          },
          legend: {
            display: false
          },
        }
    }
  },
  created() {
    var ctx = document.getElementById("line-chart").getContext('2d');
    let gradientStroke = ctx.createLinearGradient(0, 0, 0, 200);
    gradientStroke.addColorStop(0, "#f7fdff");
    gradientStroke.addColorStop(1, "#bdf5ff");

    this.hashrateBackground = gradientStroke;
  },
  mounted() {
    this.fillData();
    /*
    var ctx = document.getElementById("hashrateHistoricalChart").getContext('2d');

    var gradientStroke = ctx.createLinearGradient(0, 0, 0, 200);
    gradientStroke.addColorStop(0, "#f7fdff");
    gradientStroke.addColorStop(1, "#bdf5ff");

    var myChart = new Chart(ctx, {
        type: 'line',
        data: {
          labels: [1, 2, 3, 4, 5, 6],
          datasets: [{            
            data: [5, 3, 7, 1, 6, 3, 8, 5],
            backgroundColor: gradientStroke,
            borderColor: '#d4f9ff',
            borderWidth: 1,
            pointRadius: 0
          }]
        },
        options: {
          responsive: true,
          
          scales: {
            xAxes: [{              
              ticks: {
                beginAtZero: true,                
              },
              gridLines: {
                zeroLineColor:"#82e6f6",
                color: "transparent",
                display: true,
                drawBorder: false,
                zeroLineWidth: 1
              }
            }],
            yAxes: [{
              ticks: {
                beginAtZero:true,
                callback: function(tick) {
                  return tick.toString() + ' TH/s';
                }
              },
              gridLines: {                
                zeroLineColor:"#82e6f6",
                color: "transparent",
                display: true,
                drawBorder: false,
                zeroLineWidth: 1
              }
            }]
          },
          elements: { 
            point: { 
              radius: 0 
            } 
          },
          legend: {
            display: false
          },
        }
    });*/
  },
  methods : {
    onAlgorythmChanged(value) {
      minekamp.hashrate(value.id, function(r){
        console.log(r);
      });
    },
    show_hashrate(name) {
      minekamp.hashrate(name, function(r){
        console.log(r);
      });
    },

    fillData () {
      
        this.datacollection = {
          labels: [1, 2, 3, 4, 5, 6],
          datasets: [{            
            data: [5, 3, 7, 1, 6, 3, 8, 5],
            backgroundColor: this.hashrateBackground,
            borderColor: '#d4f9ff',
            borderWidth: 1,
            pointRadius: 0
          }]
        }
      },
      getRandomInt () {
        return Math.floor(Math.random() * (50 - 5 + 1)) + 5
      }
  }
}
</script>

<style lang="scss">
  @import "../styles/var.scss";

  .hashrate {
    .mk-panel-body {
      @media (min-width: $tablet-min-width) {
        padding-top: 0;
      }
    }
    .hashrate-caption {      
      text-align: left;
      color: #0f0f0f;

      @media (min-width: $tablet-min-width) {
        float: left;
      }

      h2 {
        font-size: 24px;
        font-weight: bold;        
      }
      small {
        display: inline-block;
        padding-top: 15px;
        font-size: 14px;
        text-align: left;
        color: #d8d8d8;
      }
    }
    .algorythm-buttons {
      width: 100%;
      @media (min-width: $tablet-min-width) {
        max-width: 266px;
        float: right;
      }
    }
  }
  .chart-container {
    position: relative;
    padding-top: 40px;
    padding-bottom: 23px;

    #hashrateHistoricalChart {
      height: 193px;
    }    
  }
  
</style>



